import { createSelector } from 'reselect';
import { IStore } from '../../types';

export const selectBooks = createSelector(
	({ books }: IStore) => books,
	(books) => books,
);

export const selectAllBooks = createSelector(selectBooks, (books) => books.booksList);
export const selectedBook = createSelector(selectBooks, (books) => books.selectedBook);
export const randomizedBook = createSelector(selectBooks, (books) => books.randomizedBook);
export const modalWindowState = createSelector(selectBooks, (books) => books.isModalWindowOpen);
