import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { IBooksState } from '../../types';
import { BOOKS_URL } from '../../constants';

const initialState: IBooksState = {
	booksList: [],
	selectedBook: null,
	randomizedBook: null,
	isModalWindowOpen: false,
};

export const fetchBooks = createAsyncThunk('books/fetchBooks', async () => {
	const response = await fetch(BOOKS_URL);
	const data = await response.json();
	return await data;
});

export const booksSlice = createSlice({
	name: 'books',
	initialState,
	reducers: {
		selectBook(state, action) {
			state.selectedBook = action.payload;
		},
		randomizeBook(state, action) {
			state.randomizedBook = action.payload;
		},
		toggleModalWindow(state, action) {
			state.isModalWindowOpen = action.payload;
		},
	},
	extraReducers: (builder) => {
		builder.addCase(fetchBooks.fulfilled, (state: IBooksState, action) => {
			state.booksList = [...state.booksList, ...action.payload];
		});
	},
});

export const { selectBook, randomizeBook, toggleModalWindow } = booksSlice.actions;
export default booksSlice.reducer;
