import { Link } from 'react-router-dom';
import './index.css';
import { selectAllBooks } from '../../shared/store/books/selectors';
import { useSelector } from 'react-redux';
import { useState, useEffect } from 'react';
import { IBook } from '../../shared/types';
import { useAppDispatch } from '../../shared/store';
import { randomizeBook, toggleModalWindow } from '../../shared/store/books/booksSlice';
import { Button } from '../Button';

export const Header = () => {
	const [bookId, setBookId] = useState(0);
	const [book, setBook] = useState<IBook>();
	const booksList = useSelector(selectAllBooks);
	const booksId = booksList.map((book) => book.id);
	const dispatch = useAppDispatch();

	const handleRandom = () => {
		const selectedBookId = Math.floor(Math.random() * booksId.length);
		setBookId(selectedBookId);
		dispatch(toggleModalWindow(true));
	};

	useEffect(() => {
		setBook(booksList.find((book) => book.id === bookId));
	}, [bookId]);

	useEffect(() => {
		dispatch(randomizeBook(book));
	}, [book]);

	return (
		<div className="headerContainer">
			<div className="imgWrapper">
				<Link to={'/'}>
					<div className="logo" />
				</Link>
			</div>
			<Button title="Find a book" handler={handleRandom} />
		</div>
	);
};
