import { useSelector } from 'react-redux';
import { selectAllBooks, selectedBook } from '../../shared/store/books/selectors';
import './index.css';
import { useAppDispatch } from '../../shared/store';
import { fetchBooks, selectBook } from '../../shared/store/books/booksSlice';
import { useParams } from 'react-router-dom';
import { useEffect } from 'react';

export const BookPage = () => {
	const { id } = useParams();
	const bookId = Number(id);
	const booksList = useSelector(selectAllBooks);
	const book = useSelector(selectedBook);

	const dispatch = useAppDispatch();

	useEffect(() => {
		dispatch(fetchBooks());
	}, []);

	useEffect(() => {
		const book = booksList.find((elem) => elem.id === bookId);
		dispatch(selectBook(book));
	}, [booksList, id]);

	return (
		<div className="bookPageWrapper">
			<p className="title">{book?.title}</p>
			<div className="info">
				<p>{book?.authors}</p>
				<p>{book?.rating}</p>
			</div>
			<div className="bookDescription">
				<img src={book?.image_url} alt="Image" />
				<div className="fullDescription">{book?.description}</div>
			</div>
			<p className="genres">Genres: {book?.genres}</p>
		</div>
	);
};
