import './index.css';
import { useSelector } from 'react-redux';
import { modalWindowState, randomizedBook } from '../../shared/store/books/selectors';
import { Button } from '../Button';
import { useAppDispatch } from '../../shared/store';
import { toggleModalWindow, randomizeBook } from '../../shared/store/books/booksSlice';
import { Link } from 'react-router-dom';

export const BookViewer = () => {
	const dispatch = useAppDispatch();
	const isOpen = useSelector(modalWindowState);
	const randomBook = useSelector(randomizedBook);

	const handleClose = () => {
		dispatch(toggleModalWindow(false));
		dispatch(randomizeBook(null));
	};

	return (
		<div className={isOpen ? 'book-viewer-wrapper-opened' : 'book-viewer-wrapper'}>
			<div className="book-container">
				<Button title="X" handler={handleClose} />
				{randomBook?.id ? (
					<>
						<h2>{randomBook?.title}</h2>
						<p className="book-author">{randomBook?.authors}</p>
						<div className="imgWrapper">
							<img src={randomBook?.image_url} alt="Cover" />
						</div>
						<p className="book-rating">Rating: {randomBook?.rating}</p>
						<Link className="desc-link" to={`books/${randomBook?.id}`} onClick={handleClose}>
							Description
						</Link>
					</>
				) : (
					<>
						<p className="error">Oooops... Something went wrong. Try again...</p>
					</>
				)}
			</div>
		</div>
	);
};
